"use client"

import FormItem from "antd/es/form/FormItem"
import { useState } from "react"
import { useForm } from "antd/es/form/Form"
import { Button, Flex, Form, Input, message, Select } from "antd"
import { useRouter } from "next/navigation"
import { RoutesEnum } from "../../../shared/router/routesEnum"
import { RegisterCredentials } from "../types"
import { registerRequest } from "../api"
import { courses } from "../types/courses"
import { groups } from "../types/groups"
import Link from "next/link"

export const RegisterUserUI = () => {
  const [isLoading, setLoading] = useState<boolean>(false)

  const [form] = useForm<RegisterCredentials>()

  const router = useRouter()

  const LogInHandler = () => {
    router.push(RoutesEnum.LOGIN)
  }

  const successAction = (payload: RegisterCredentials) => {
    setLoading(true)
    registerRequest(payload)
      .then(() => {
        router.push(RoutesEnum.HOME)
      })
      .catch(error => {
        message.error(error.message || "Ошибка регистрации")
      })
      .finally(() => {
        setLoading(false)
      })
  }

  return (
    <>
      <Form.Provider
        onFormFinish={name => {
          if (name === "userForm") {
            // Do something...
          }
        }}
      >
        <Form
          form={form}
          layout='vertical'
          name='registerForm'
          onFinish={successAction}
        >
          <FormItem
            label={"Логин"}
            name='login'
            required
          >
            <Input
              type='email'
              placeholder='username@gmail.com'
            />
          </FormItem>
          <FormItem
            label={"ФИО"}
            name='name'
            required
          >
            <Input
              type='text'
              placeholder='Иванов Иван Иванович'
            />
          </FormItem>
          <FormItem
            label={"Курс"}
            name='course'
            required
          >
            <Select
              defaultValue={1}
              options={courses}
            />
          </FormItem>
          <FormItem
            name={"group"}
            label={"Группа"}
            required
          >
            <Select
              defaultValue={"972102"}
              options={groups}
            />
          </FormItem>
          <FormItem
            name={"password"}
            label={"Пароль"}
            required
          >
            <Input
              type='password'
              placeholder='*********'
            />
          </FormItem>
          <FormItem
            name={"repeatPassword"}
            label={"Повторите пароль"}
            required
          >
            <Input
              type='repeat-password'
              placeholder='*********'
            />
          </FormItem>
          <FormItem>
            <Flex gap={"16px"}>
              <Link href={RoutesEnum.LOGIN}>Войти</Link>
              <Button
                block
                loading={isLoading}
                type='primary'
                htmlType='submit'
              >
                Зарегистрироваться
              </Button>
            </Flex>
          </FormItem>
        </Form>
      </Form.Provider>
    </>
  )
}
